import React from 'react';

export default function ContactList({ list, removeContact }) {
  return (
    <ul>
      {list.map(elem => {
        return (
          <li key={elem.id}>
            <span>{elem.name}:</span>
            <span>{elem.phone}</span>
            <button type="button" onClick={removeContact} data-key={elem.id}>
              Delete
            </button>
          </li>
        );
      })}
    </ul>
  );
}
